import Vue from "vue"
import "bulma/bulma.sass"
import VueUiTablePlugin from "./vue-ui-template-plugin"
import router from "./docs/router"
import Docs from "./docs/docs"
import "./scss/ui-table.scss"

Vue.use(VueUiTablePlugin, {
    theme: {
        classes: {
            container: "table-container",
            table: "table",
            bordered: "is-bordered",
            striped: "is-striped",
            dense: "is-narrow",
            hoverable: "is-hoverable"
        }
    }
})

Vue.config.productionTip = false

new Vue({
    router,
    render: h => h(Docs)
}).$mount("#app")